export default () => {
  const $faq = $('.faq-content');


  $('.faq-item.open').find('.fas').removeClass('fa-plus').addClass('fa-minus');

  $('.faq-item__question').on('click', function(){
    if($(this).parents('.faq-item').hasClass('open')) {
      $(this).parents('.faq-item').find('.faq-item__answer').slideUp(function(){
        $(this).parents('.faq-item').removeClass('open').find('.fas').removeClass('fa-minus').addClass('fa-plus');
      });
      
    } else {
      $(this).parents('.faq-item').find('.faq-item__answer').slideDown(function() {
        $(this).parents('.faq-item').addClass('open').find('.fas').removeClass('fa-plus').addClass('fa-minus');
      });
    }
  });
};