export default () => {
  const MOBILE_BREAKPOINT = 685;
  const TOGGLE_SPEED = 300;
  const $body = $('body');
  const $window = $(window);
  const $mainNav = $('#js-main-nav');
  const $mobileMenu = $('#js-mobile-menu');

  function closeMenu() {
    $body.removeClass('has-menu-open');
    $('.sub-menu').slideUp(TOGGLE_SPEED).parent().removeClass('is-active');
  }

  $mainNav.find('.menu-item-has-children > a').on('click', e => {
    const $this = $(e.currentTarget);

    if($body.hasClass('has-menu-open')) {
      closeMenu();
    } else {
      $body.addClass('has-menu-open');
      $this.parent().toggleClass('is-active').find('.sub-menu').stop().slideToggle(TOGGLE_SPEED);
    }

    e.preventDefault();
  });

  $(document).click(function(event) {
    if(!$(event.target).parent('.menu-item').length) {
      closeMenu();
    }
  });

  $mobileMenu.on('click', e => {
    const $this = $(e.currentTarget);
    const OPEN_CLASS = 'is-open';

    $this.toggleClass(OPEN_CLASS);
    $mainNav.stop().slideToggle(TOGGLE_SPEED);

    e.preventDefault();
  });

  $window.resize(() => {
    if ($window.width() >= MOBILE_BREAKPOINT) {
      $mainNav.removeAttr('style');
    }
  });
};
