export default () => {
  const $formWrapper = $('.gform_wrapper');
  const $description = $formWrapper.closest('.c-hero-band__description');
  const BOTTOM_OFFSET = '15.625em';

  $description
    .closest('.c-band')
    .next('section')
    .css('padding-top', BOTTOM_OFFSET);

  jQuery(document).bind('gform_confirmation_loaded', function(event, formId) {
    $('html, body').animate({ scrollTop: 0 }, 'slow');
    $('.c-band').css('padding-top', '');
  });
};
