export default () => {
  // get the cooookies
  var getCookies = function() {
    var pairs = document.cookie.split('; ');
    var cookies = {};
    for (var i = 0; i < pairs.length; i++) {
      var pair = pairs[i].split('=');
      cookies[pair[0]] = unescape(pair[1]);
    }
    return cookies;
  };
  // check to see if specifc utm has been set in the cookies
  function utmCheck(utm) {
    var cookies = getCookies();
    var uutm = 'utm_' + utm;
    var testArray = uutm in cookies;
    return testArray;
  }
  // set the cookies for the page
  var cookies = getCookies();
  // check all a's on the page
  $('a[href]').each(function() {
    // alert(jQuery(this).attr('href'));
    var utms = null;
    var tt = $(this),
      str = tt.attr('href'),
      url = str;

    if (
      str.indexOf('https://crunchbase.com') > -1 ||
      str.indexOf('https://www.crunchbase.com') > -1
    ) {
      if (
        utmCheck('source') ||
        utmCheck('content') ||
        utmCheck('medium') ||
        utmCheck('display') ||
        utmCheck('term') ||
        utmCheck('keyword') ||
        utmCheck('campaign')
      ) {
        utms = '?';
        var i = 0;
        if (utmCheck('source')) {
          utms += 'utm_source=' + cookies.utm_source;
          i++;
        }
        if (utmCheck('display')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_display=' + cookies.utm_display;
          i++;
        }
        if (utmCheck('term')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_term=' + cookies.utm_term;
          i++;
        }
        if (utmCheck('content')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_content=' + cookies.utm_content;
          i++;
        }
        if (utmCheck('medium')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_medium=' + cookies.utm_medium;
          i++;
        }
        if (utmCheck('keyword')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_keyword=' + cookies.utm_keyword;
          i++;
        }
        if (utmCheck('campaign')) {
          if (i > 0) {
            utms += '&';
          }
          utms += 'utm_campaign=' + cookies.utm_campaign;
          i++;
        }
        url += utms;
        tt.attr('href', url);
      }
    }
  });

  if ($('.gform_cookie_load').length) {
    function utmCCheck(utm) {
      var cookies = getCookies();
      var uutm = utm;
      var testArray = uutm in cookies;
      return testArray;
    }
    $('.gform_cookie_load').each(function() {
      var ip = $(this).find('input'),
        $fcookie = $(this).find('input').val();
      // console.log($fcookie+'|'+utmCCheck($fcookie));
      if (utmCCheck($fcookie)) {
        ip.attr('value', cookies[$fcookie]);
      } else {
        ip.attr('value', '');
      }
    });
  }
};
