export default () => {
  const OPEN_CLASS = 'is-open';
  const BODY_CLASS = 'has-search-open';

  const $body = $('body');
  const $document = $(document);
  const $searchIcon = $('#js-search-icon');
  const $searchOverlay = $('#js-search-overlay');
  const $searchClose = $('#js-search-close');
  const $searchInput = $('.c-search-form__input');

  function closeSearch() {
    $searchOverlay.removeClass(OPEN_CLASS);
    $body.removeClass(BODY_CLASS);
  }

  $searchIcon.on('click', e => {
    const $this = $(e.currentTarget);

    $searchOverlay.addClass(OPEN_CLASS);
    $body.addClass(BODY_CLASS);
    $searchInput.focus();

    $document.keyup(function(e) {
      // esc key pressed, close overlay
      if (e.keyCode == 27) {
        closeSearch();
      }
    });

    e.preventDefault();
  });

  $searchClose.on('click', e => {
    if (!$(e.target).hasClass('c-search-form__input')) {
      closeSearch();
    }
  });
};
