import waypoint from "./waypoint";
import sanitizeHtml from 'sanitize-html';
const Ajax = {
  ajax_url: "/wp-admin/admin-ajax.php"
}

export default () => {
  const $jsAppSort = $('#js-app-sort');
  const $jsAppSortBy = $('#js-app-sortBy');
  const $jsAppSearch = $('#js-app-search');
  const $jsAppMobileCategoryDropdown = $('#js-app-category-dropdown');
  const $jsAppSortWrapper = $('#js-app-sort-wrapper');
  const $jsAppSearchInput = $('#js-app-search-input');
  const $jsAppSearchClear = $('#js-app-search-clear');
  const categoryLink = $('.js-app-category-link');
  const ACTIVE_CLASS = 'is-active';
  $('document').ready(function() {
    $jsAppSearchInput.val('');
    show_all_apps();
  });

  $(window).on("popstate", function(e) {
    let originalState = e.originalEvent.state;
    console.log(originalState);
    if(originalState && originalState.key === 'category') {
      filter_posts_by_category(originalState.id, originalState.value);
      $jsAppSearchInput.val('');
    } else if(originalState && originalState.key === 'search') {
      searchApps(originalState.value);
    } else {
      $jsAppSearchInput.val('');
      show_all_apps();
    }
  });

  function updateRequestStatus(bool) {
    $('#js-sidebar-nav .menu').attr('data-request', bool);
    $jsAppSortWrapper.attr('data-request', bool);
  }

  function addRelevancyOption() {
    $jsAppSort.append('<option>Relevancy</option>');
    let count = $('#js-app-sort option').size();
    $jsAppSort.prop('selectedIndex', count - 1).selectric('refresh');
    $jsAppSortBy.addClass('is-disabled');
    $('#js-app-sortBy .selectric-items li[data-index]:not(.last)').remove();
  }

  function removeRelevancyOption() {
    const $lastOption = $('#js-app-sort option:last');
    $jsAppSortBy.removeClass('is-disabled');

    if($lastOption.text() == 'Relevancy') {
      $lastOption.remove();
    }
  }

  function show_all_apps() {
    if ($('#js-sidebar-nav .menu').attr('data-request') === 'true') {
      return;
    }

    jQuery.ajax({
      method: 'POST',
      url: Ajax.ajax_url,
      data: {
        action: 'filter_posts'
      },
      beforeSend: function() {
        categoryLink.removeClass(ACTIVE_CLASS);
        updateRequestStatus(true);
        $('#js-loading').show();
        $('#js-post-response-wrapper').remove();
      },
      success: function(result) {
        $jsAppSort.prop('selectedIndex', 3).selectric('refresh');
        $jsAppMobileCategoryDropdown.prop('selectedIndex', 0).selectric('refresh');
        $('#js-loading').hide();
        $(`<div id="js-post-response-wrapper">${result}</div>`).prependTo(
          '#js-post-response'
        );
      },
        complete: function() {
          updateRequestStatus(false);
          waypoint();
        },
        error: function(xhr, status, error) {
          console.log(error);
        }
      });
    }

    function removeQueryParams() {
      var href = window.location.href;
      if(href.indexOf('?')) {
        var url = href.split("?")[0];
        history.pushState(null, null, url);
      }
    }

    function changeUrl(key, value, id) {
      //Get query string value
      var loc = location.search;
      if (loc.indexOf('?') == '-1') {
        var urlValue = '?' + key + '=' + value;
        history.pushState({ key, value, id }, '', urlValue);
      } else {
        //Check for key in query string, if not present
        if (loc.indexOf(key) == '-1') {
          var urlValue = loc + '&' + key + '=' + value;
        } else {
          //If key present in query string
          let oldValue = getParameterByName(key);
          if (loc.indexOf('?' + key + '=') != '-1') {
            urlValue = loc.replace(
              '?' + key + '=' + encodeURIComponent(oldValue),
              '?' + key + '=' + value
            );
          } else {
            urlValue = loc.replace(
              '&' + key + '=' + oldValue,
              '&' + key + '=' + value
            );
          }
        }
        history.pushState({ key, value, id }, '', urlValue);
      }
    }

    function filter_posts_by_category(cat_id, cat_slug) {
      const el = $('#js-sidebar-nav [data-term-slug="'+cat_slug+'"]');

      if (el.parent().parent().attr('data-request') === 'true') {
        return;
      }

      if (el.hasClass(ACTIVE_CLASS)) {
        removeQueryParams();
        show_all_apps();

        return;
      }

      let total_posts = -1;
      const data = {
        action: 'filter_posts',
        cat_id,
        cat_slug,
        posts: total_posts
      };

      updateRequestStatus(true);
      jQuery.ajax({
        method: 'POST',
        url: Ajax.ajax_url,
        data: data,

        beforeSend: function() {
          categoryLink.removeClass(ACTIVE_CLASS);
          el.addClass(ACTIVE_CLASS);
          $('#js-loading').show();
          $('#js-post-response-wrapper').remove();
        },
        success: function(result) {

          // We need to make sure to sync the mobile category dropdown to the correct index if we are filtering via the desktop sidebar links
          let mobile_cat_element = $jsAppMobileCategoryDropdown.find(`[data-term-id='${cat_id}']`);
          let mobile_cat_index = $jsAppMobileCategoryDropdown.find('option').index(mobile_cat_element);
          $jsAppMobileCategoryDropdown.prop('selectedIndex', mobile_cat_index).selectric('refresh');

          removeRelevancyOption();
          $jsAppSort.prop('selectedIndex', 3).selectric('refresh');
          $('#js-loading').hide();
          $(`<div id="js-post-response-wrapper">${result}</div>`).prependTo(
            '#js-post-response'
          );
        },
        complete: function() {
          if (history.pushState) {
            removeQueryParams();
            changeUrl('category', cat_slug, cat_id);
          }

          updateRequestStatus(false);
          waypoint();
        },
        error: function(xhr, status, error) {
          console.log(error);
        }
      });
    }

    categoryLink.on('click', e => {
      let $this = $(e.currentTarget);
      let term_id = $this.attr('data-term-id');
      let term_slug = $this.attr('data-term-slug');

      if($jsAppSearchInput.val()) {
        $jsAppSearchInput.removeAttr('value');
        $jsAppSearchClear.removeClass(ACTIVE_CLASS);
      }

      filter_posts_by_category(term_id, term_slug);

      return false;
    });

    // Search
    function searchApps(searchTerm) {
      searchTerm = sanitizeHtml(searchTerm);

      if ($('#js-sidebar-nav .menu').attr('data-request') === 'true') {
        return;
      }

      $jsAppSearchInput.val(searchTerm);

      jQuery.ajax({
        method: 'POST',
        url: Ajax.ajax_url,
        data: {
          action: 'filter_posts',
          searchTerm
        },

        beforeSend: function() {
          $jsAppMobileCategoryDropdown.prop('selectedIndex', 0).selectric('refresh');
          categoryLink.removeClass(ACTIVE_CLASS);
          updateRequestStatus(true);
          $('#js-loading').show();
          $('#js-post-response-wrapper').remove();
        },
        success: function(result) {
          if(searchTerm) {
            addRelevancyOption();
          } else {
            removeRelevancyOption();
            $jsAppSort.prop('selectedIndex', 3).selectric('refresh');
          }

          $('#js-loading').hide();
          $(`<div id="js-post-response-wrapper">${result}</div>`).prependTo(
            '#js-post-response'
          );
        },
        complete: function() {
          if (history.pushState) {
            removeQueryParams();
            if(searchTerm) {
              changeUrl('search', searchTerm, searchTerm);
            }
          }

          updateRequestStatus(false);
          waypoint();
        },
        error: function(xhr, status, error) {
          console.log(error);
        }
      });
    }

    $jsAppSearch.on('submit', e => {
      let searchTerm = $jsAppSearchInput.val();

      searchApps(searchTerm);

      // blur the input, which closes the keyboard on a phone
      document.activeElement.blur();

      return false;
    });

    $jsAppSearchInput.on('keyup', e => {
      let $this = $(e.currentTarget);
      let val = $this.val();
      if(val) {
        $jsAppSearchClear.addClass(ACTIVE_CLASS);
      } else {
        $jsAppSearchClear.removeClass(ACTIVE_CLASS);
      }
    });

    $jsAppSearchClear.on('click', e => {
      $jsAppSearchInput.removeAttr('value');
      $jsAppSearchClear.removeClass(ACTIVE_CLASS);
      searchApps('');

      return false;
    });

    $jsAppSort.selectric({
      disableOnMobile: false,
      nativeOnMobile: false,
      onInit: function(element) {
        const searchTerm = $jsAppSearchInput.val();
        if(searchTerm) {
          setTimeout(function() {
            addRelevancyOption();
          }, 100)
        }
      },
      onChange: function(element) {
        const searchTerm = $jsAppSearchInput.val();
        const cat_id = $('.js-app-category-link.is-active').data('term-id');
        const cat_slug = $('.js-app-category-link.is-active').data('term-slug');
        const sortValue = $(element).find(':selected').val();

        jQuery.ajax({
          method: 'POST',
          url: Ajax.ajax_url,
          data: {
            action: 'filter_posts',
            cat_id,
            cat_slug,
            searchTerm,
            sortValue
          },
          beforeSend: function() {
            updateRequestStatus(true);
            $('#js-loading').show();
            $('#js-post-response-wrapper').remove();
          },
          success: function(result) {
            $('#js-loading').hide();
            $(`<div id="js-post-response-wrapper">${result}</div>`).prependTo(
              '#js-post-response'
            );
          },
          complete: function() {
            updateRequestStatus(false);
            waypoint();
        },
        error: function(xhr, status, error) {
          console.log(error);
        }
      });
    },
  });

  $jsAppMobileCategoryDropdown.selectric({
    disableOnMobile: false,
    nativeOnMobile: false,
    onChange: function(element) {
      let term_id = $(element).find(':selected').data('term-id');
      let term_slug = $(element).find(':selected').data('term-slug');

      // clear search term
      $jsAppSearchInput.removeAttr('value');

      if(term_id === 'all') {
        removeQueryParams();
        show_all_apps();
      } else {
        filter_posts_by_category(term_id, term_slug);
      }
    }
  });
};
