export default () => {
  const $sidebarNav = $('#js-sidebar-nav');
  const $header = $('#js-header');

  function scrollToAnchor($this) {
    const headerHeight = $header.outerHeight();

    if($this.selector) {
      $('html,body').animate({ scrollTop: $this.offset().top - headerHeight }, 500);
    }
  }

  // topics dropdown
  $sidebarNav.find('.js-topics-link').on('click', e => {
    const $this = $(e.currentTarget);

    $this.toggleClass('is-active').parent().find('.sub-menu').slideToggle(300);

    e.preventDefault();
  });

  // scrolling to an anchor
  $('a[href*=\\#]').on('click', function(e) {

    scrollToAnchor($(this.hash));

    e.preventDefault();
  });

  // check if hash on page
  if(window.location.hash) {
    const hash = window.location.hash;

    scrollToAnchor($(hash));
  }
};
