export default () => {
  const $window = $(window);

  function waypoint() {
    if ($window.width() >= 540) {
      let waypoints = $(".h-waypoint").waypoint({
        handler: function(direction) {
          if (direction == "down") {
            $(this.element).addClass("is-loaded");
          }
        },
        offset: "100%"
      });
    }
  }

  waypoint();
}
