export default () => {
  const ACTIVE_CLASS = 'is-active';
  const $planLink = $('.js-plan-link');
  const $planItem = $('.js-plan-item');
  const $planCell = $('.js-plan-cell');

  $planLink.on('click', e => {
    const $this = $(e.currentTarget);

    if($this.parent().hasClass(ACTIVE_CLASS)) return false;

    const slug = $this.data('slug');

    $planCell.removeClass(ACTIVE_CLASS);
    $planItem.removeClass(ACTIVE_CLASS);
    $this.parent().addClass(ACTIVE_CLASS);

    $('#js-plan--' + slug).addClass(ACTIVE_CLASS);

    e.preventDefault();
  });
};
